/**
 * SAVE: WDS Card Builder
 */
import { RichText } from '@wordpress/block-editor';

const Save = ( props ) => {
	const {
		attributes: {
			content,
		},
		className,
	} = props;

	return (
		<RichText.Content
			className={ className }
			tagName="p"
			value={ content }
		/>
	);
};

export default Save;
